import React, { useEffect, useRef, useState } from "react";

function TodoItem({ todo, todos, setTodos }) {
  const [checked, setChecked] = useState(todo.done);
  const [edit, setEdit] = useState(false);
  const [showPopover, setShowPopover] = useState(false);
  const [todoEdit, setTodoEdit] = useState(todo.title);
  const [tagName, setTagName] = useState(todo.tag);
  const tags = {
    Work: "#ffbe0b",
    Life: "#fb5607",
    Personal: "#ff006e",
    Travel: "#8338ec",
    School: "#3a86ff",
  };

  // const EditInput = React.forwardRef((props, ref) => {
  //   return (
  //     <input
  //       type="text"
  //       name="edit"
  //       id=""
  //       value={todoEdit}
  //       className="h-full border-none flex-1"
  //       onChange={(e) => setTodoEdit(e.target.value)}
  //       ref={ref}
  //     />
  //   );
  // });
  const editInputRef = useRef(null);

  var loc;
  for (let i = 0; i < todos.length; i++) {
    const element = todos[i];
    if (todo === element) {
      loc = i;
    }
  }
  var currentTodos = todos;
  var currentTodo = todo;
  function handleSubmitEdit(e) {
    e.preventDefault();
    if (todoEdit) {
      currentTodos.splice(loc, 1);
      currentTodo.title = todoEdit;
      currentTodo.done = false;
      currentTodos.splice(loc, 0, currentTodo);
      setEdit(false);
    }
  }
  function changeTagName(tag) {
    setTagName(tag);
    currentTodo.tag = tag;
    currentTodos.splice(loc, 1);
    currentTodos.splice(loc, 0, currentTodo);
    setTodos(currentTodos);
  }
  useEffect(() => {
    if (checked) {
      currentTodo.done = true;
      currentTodos.splice(loc, 1);
      currentTodos.splice(loc, 0, currentTodo);
      setTodos(currentTodos);
    } else {
      currentTodo.done = false;
      currentTodos.splice(loc, 1);
      currentTodos.splice(loc, 0, currentTodo);
      setTodos(currentTodos);
    }
    console.log("effect");
    console.log(currentTodo.done);
    console.log(currentTodos[loc].done);
    console.log(todos[loc].done);
  }, [checked]);

  useEffect(() => {
    edit && editInputRef.current.focus();
  }, [edit]);

  if (edit) {
    return (
      <button
        className=" gap-24 rounded-md bg-gray-100 focus:bg-gray-300 p-4 w-full "
        onClick={() => setChecked(!checked)}
      >
        <form
          onSubmit={(e) => handleSubmitEdit(e)}
          className="flex items-center gap-4 w-full"
        >
          <input
            type="text"
            name="edit"
            id=""
            value={todoEdit}
            className="h-full px-2  text-semibold rounded border border-gray-300 outline-none flex-1 focus:ring-2 focus:ring-blue-400 "
            onChange={(e) => setTodoEdit(e.target.value)}
            ref={editInputRef}
          />
          <button
            className="bg-blue-500 text-semibold text-white px-4 rounded-md flex items-center justify-center"
            onClick={(e) => handleSubmitEdit(e)}
            type="submit"
          >
            Done
          </button>
        </form>
      </button>
    );
  }
  return (
    <button
      className="flex rounded-md bg-gray-100 hover:bg-gray-200 focus:bg-gray-400 p-4 items-center  w-full"
      onClick={() => setChecked(!checked)}
    >
      <div className="flex gap-6 flex-1">
        <input
          type="checkbox"
          name=""
          id=""
          className="w-5 h-5"
          checked={checked}
          onClick={() => setChecked(!checked)}
        />
        <p className={checked ? "font-semibold line-through" : "font-semibold"}>
          {todo.title}
        </p>
      </div>
      <div className="flex space-x-2">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6 text-gray-600 hover:bg-gray-400 rounded"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          onClick={() => setEdit(true)}
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z"
          />
        </svg>
        <div className="relative group">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 "
            fill={tagName==='none' ? 'none':tags[tagName]}
            viewBox="0 0 24 24"
            stroke={tagName==='none'?'blue':tags[tagName]}
            onClick={() => setShowPopover(!showPopover)}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 5a2 2 0 012-2h10a2 2 0 012 2v16l-7-3.5L5 21V5z"
            />
          </svg>
          {showPopover && (
            <div className="absolute top-6 right-0 md: text-center w-24  pt-6 pb-2 rounded bg-black z-10 transition text-white flex flex-col gap-4">
              <h1
                className="absolute top-0 right-0 text-center hover:border-gray-500"
                onClick={() => setShowPopover(!showPopover)}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-6 w-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </h1>
              {Object.keys(tags).map((tag) => {
                return (
                  <p
                    className="hover:bg-gray-500 w-full text-center"
                    onClick={() => changeTagName(tag)}
                  >
                    {tag}
                  </p>
                );
              })}
            </div>
          )}
          <div className="absolute -top-10 right-0 opacity-0   text-center w-24 group-hover:opacity-100 pointer-events-none px-4 py-2 rounded bg-gray-700 transition text-white">
            {tagName}
          </div>
        </div>
      </div>
    </button>
  );
}

export default TodoItem;
